import React from 'react';

class Navigation extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            links: this.props.pages,
        }
    }

    generateKey = (pre) => {
        return `${pre}_${new Date().getTime()}`;
    }

    render() {
        return (
            <nav>
                <div className="nav-wrapper">
                    <a href="{ this.props.pages.home.linkTo }" className="brand-logo center">UMEET</a>
                    <ul className="left hide-on-med-and-down">
                        {
                            Object.keys(this.state.links).map(link => {
                                Object.keys(link).map(properties => {
                                    {
                                        Object.keys(properties).map(property => (
                                            <li key={this.generateKey(property)}>
                                                <a href="{link[2]}">
                                                    {property}
                                                </a>
                                            </li>
                                        ))
                                    }
                                })
                            }
                            )
                        }
                    </ul>
                </div>
            </nav>
        )
    }
}

export default Navigation;